<template>
  <div>
    <ec-drawer title="新增" :visible="visible" size="large" @close="onCancel" :is-confirm="true">
      <ec-container height="100%">
        <ec-container-body>
          <ec-panel :auto="false">
            <ec-panel-toolbar>
              <ec-query-panel :defaultFold="true" @query="onQuery" @reset="onReset">
                <ec-query-item label="基地单元名称">
                  <a-input v-model.trim="query.baseCellName" placeholder="请输入" />
                </ec-query-item>
                <ec-query-item label="对口工业企业">
                  <select-picker
                    v-model.trim="query.linkInduOrgCode"
                    placeholder="请选择对口工业企业"
                    :lazy="false"
                    :search="false"
                    :fetchData="onFetchIndustryOrgList"
                  />
                </ec-query-item>
              </ec-query-panel>
              <ec-divider />
            </ec-panel-toolbar>
            <ec-panel-body>
              <ec-v-layout :auto="false">
                <!-- 表格工具栏 -->
                <ec-v-item height="auto">
                  <ec-panel-toolbar>
                    <ec-panel-item-left>
                      <span>操作类型：</span>
                      <ec-select placeholder="请选择" v-model="operateType">
                        <a-select-option v-for="item in operateTypeList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </ec-panel-item-left>
                    <ec-panel-item-right>
                      <div class="unit-box">
                        <span>业务年度：{{ busiYear }}年</span>
                        <div class="select">
                          重量单位：
                          <enum-select v-model="weightUnit" :enumTypeCodes="['WEIGHT_UNIT']" />
                        </div>
                      </div>
                    </ec-panel-item-right>
                  </ec-panel-toolbar>
                </ec-v-item>
                <!-- 列表 -->
                <ec-v-item>
                  <ec-table
                    ref="tableRef"
                    show-overflow
                    height="100%"
                    :auto="false"
                    :data="tableData"
                    :edit-config="{ trigger: 'click', mode: 'row' }"
                  >
                    <ec-table-col type="seq" title="序号" width="50" />
                    <ec-table-col
                      field="baseCellName"
                      title="基地单元名称"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="leafTypeCode"
                      title="烟叶类型"
                      align="center"
                      min-width="100"
                    >
                      <template #default="{ row }">
                        <div>
                          {{
                            row.leafTypeCode
                              | getNameBy(leafTypeList, {
                                key: 'leafTypeCode',
                                label: 'leafTypeName',
                              })
                          }}
                        </div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="provOrgName"
                      title="省公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="cityOrgName"
                      title="市公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="linkInduProvOrgName"
                      title="省级工业公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="linkInduOrgName"
                      title="对口工业企业"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col field="baApprWeight" title="调整前" align="right" min-width="150">
                      <template #default="{ row }">
                        <div>{{ row.baApprWeight | amountFormat(weightUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="apprWeightTrans"
                      title="本次调整"
                      align="right"
                      min-width="150"
                    >
                      <template #default="{ row }">
                        <div>{{ row.apprWeightTrans | amountFormat(weightUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col field="aaApprWeight" title="调整后" align="right" min-width="150">
                      <template #default="{ row }">
                        <div>{{ row.aaApprWeight | amountFormat(weightUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col field="remark" title="办理依据" align="left" min-width="150" />
                  </ec-table>
                </ec-v-item>
              </ec-v-layout>
            </ec-panel-body>
            <ec-panel-footer>
              <ec-panel-item-right>
                <a-button type="primary" @click="onSave">保存</a-button>
                <a-button type="default" @click="onCancel">关闭</a-button>
              </ec-panel-item-right>
            </ec-panel-footer>
          </ec-panel>
        </ec-container-body>
      </ec-container>
    </ec-drawer>
  </div>
</template>
<script>
import { EnumSelect, SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'AddModal',
  components: {
    EnumSelect,
    SelectPicker,
  },
  props: {
    leafTypeList: {
      type: Array,
      default: () => [],
    },
    industryOrgList: {
      type: Array,
      default: () => [],
    },
    replaceFields: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
  },
  data() {
    return {
      visible: false,
      query: {},
      tableData: [],
      operateTypeList: [
        { enumKey: '0', name: '正常' },
        { enumKey: '1', name: '增量' },
        { enumKey: '2', name: '减量' },
      ],
      operateType: '0',
      // 当前重量单位
      weightUnit: '500000',
    };
  },
  methods: {
    show() {
      this.visible = true;
      this.onQuery();
    },
    // 更新工业单位
    onFetchIndustryOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.industryOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 查询
    onQuery() {
      const params = {
        operateType: this.operateType,
        busiYear: this.busiYear,
        ...this.query,
      };
      $ec.showLoading();
      this.$apis.apm.baseDailyMaintain
        .addQuery(params)
        .then((res) => {
          $ec.hideLoading();
          this.tableData = res?.rows || [];
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 重置
    onReset() {
      this.query = {};
      this.onQuery();
    },
    // 取消
    onCancel() {
      this.visible = false;
    },
    // 保存
    onSave() {
      const params = {
        busiYear: this.busiYear,
        operatorType: this.operateType,
        pcTBmUnitApprQtyDTOList: this.tableData,
      };
      $ec.showLoading();
      this.$apis.apm.baseDailyMaintain.save(params).then(() => {
        $ec.hideLoading();
        this.$message.error('保存成功');
        this.$emit('saved');
        this.visible = false;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
