<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-toolbar>
        <ec-header title="基地单元日常交易计划量维护" icon="onetomany" color-code="003" />
      </ec-panel-toolbar>
    </ec-container-header>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="onQuery" @reset="onReset">
            <ec-query-item label="发送状态">
              <a-select
                showSearch
                allow-clear
                placeholder="请选择"
                v-model="queryItems.transStates"
              >
                <a-select-option v-for="s in sendStateOptions" :key="s.id">
                  {{ s.label }}
                </a-select-option>
              </a-select>
            </ec-query-item>
            <ec-query-item label="商业单位">
              <select-picker
                title="选择商业单位"
                :lazy="false"
                :search="false"
                :fetchData="onFetchBusinessOrgList"
                v-model="queryItems.provOrgCode"
              />
            </ec-query-item>
            <ec-query-item label="对口工业企业">
              <select-picker
                v-model.trim="queryItems.linkInduOrgCode"
                placeholder="请选择对口工业企业"
                :lazy="false"
                :search="false"
                :fetchData="onFetchIndustryOrgList"
              />
            </ec-query-item>
            <template #inner>
              <ec-query-item label="烟叶类型">
                <leaf-select v-model="queryItems.leafTypeCode" useType="TYPE" />
              </ec-query-item>
              <ec-query-item label="基地单元名称">
                <a-input v-model="queryItems.baseCellName" placeholder="" autocomplete="off" />
              </ec-query-item>
            </template>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <!-- 表格工具栏 -->
        <ec-panel-item>
          <ec-panel-item-left>
            <a-button type="primary" @click="onGenerate">生成</a-button>
            <a-button @click="onAdd">新增</a-button>
            <a-button @click="onDelete">删除</a-button>
            <a-button @click="onSend">发送</a-button>
            <a-button>导出</a-button>
          </ec-panel-item-left>
          <ec-panel-item-right>
            <div class="unit-box">
              <span>业务年度：{{ busiYear }}年</span>
              <div class="select">
                重量单位：
                <enum-select v-model="weightUnit" :enumTypeCodes="['WEIGHT_UNIT']" />
              </div>
            </div>
          </ec-panel-item-right>
        </ec-panel-item>
        <ec-panel-body>
          <ec-table
            ref="tableRef"
            show-overflow
            height="100%"
            :auto="false"
            :data="tableData"
            @checkbox-all="onSelAllTableData"
            @checkbox-change="onSelTableData"
          >
            <ec-table-col type="checkbox" align="center"></ec-table-col>
            <ec-table-col type="seq" title="序号" width="60" />
            <ec-table-col field="createTime" title="调整日期" align="center" width="120" />
            <ec-table-col field="provOrgName" title="省公司" align="left" min-width="150" />
            <ec-table-col field="cityOrgName" title="市公司" align="left" min-width="150" />
            <ec-table-col
              field="linkInduProvOrgName"
              title="省级工业公司"
              align="left"
              min-width="150"
            />
            <ec-table-col
              field="linkInduOrgName"
              title="对口工业企业"
              align="left"
              min-width="150"
            />
            <ec-table-col field="baseCellName" title="基地单元" align="left" min-width="150" />
            <ec-table-col field="leafTypeCode" title="烟叶类型" align="left" min-width="100">
              <template #default="{ row }">
                <div>
                  {{
                    row.leafTypeCode
                      | getNameBy(leafTypeList, { key: 'leafTypeCode', label: 'leafTypeName' })
                  }}
                </div>
              </template>
            </ec-table-col>
            <ec-table-col field="baApprWeight" title="调整前" align="right" min-width="150">
              <template #default="{ row }">
                <div>{{ row.baApprWeight | amountFormat(weightUnit) }}</div>
              </template>
            </ec-table-col>
            <ec-table-col field="apprWeightTrans" title="本次调整" align="right" min-width="150">
              <template #default="{ row }">
                <div>{{ row.apprWeightTrans | amountFormat(weightUnit) }}</div>
              </template>
            </ec-table-col>
            <ec-table-col field="aaApprWeight" title="调整后" align="right" min-width="150">
              <template #default="{ row }">
                <div>{{ row.aaApprWeight | amountFormat(weightUnit) }}</div>
              </template>
            </ec-table-col>
            <ec-table-col field="remark" title="办理依据" align="left" min-width="150" />
            <ec-table-col field="transStates" title="发送状态" align="left" width="100">
              <template #default="{ row }">
                <div v-if="row.transStates === '1'">
                  <span style="color: #409eff; margin-right: 4px">●</span>
                  <span>已发送</span>
                </div>
                <div v-else style="color: rgba(0, 0, 0, 0.447)">
                  <span style="margin-right: 4px">●</span>
                  <span>未发送</span>
                </div>
              </template>
            </ec-table-col>
            <!-- <ec-table-col title="操作" width="100" align="center">
              <template #default="{ row }">
                <a-button type="link" @click="onEdit(row)">编辑</a-button>
              </template>
            </ec-table-col> -->
          </ec-table>
        </ec-panel-body>
      </ec-panel>
      <edit-modal
        ref="editModalRef"
        :industryOrgList="industryOrgList"
        :replaceFields="replaceFields"
        :leafTypeList="leafTypeList"
        @saved="onQuery"
      />
    </ec-container-body>
  </ec-container>
</template>

<script>
import EditModal from './EditModal.vue';
import { EnumSelect, LeafSelect, SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'BaseDailyMaintain',
  components: { EditModal, EnumSelect, LeafSelect, SelectPicker },
  data() {
    return {
      // 商业单位
      businessOrgList: [],
      // 工业单位
      industryOrgList: [],
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
      // 重量单位
      weightUnit: '500000',
      // 发送状态
      sendStateOptions: [
        { id: '0', label: '未发送' },
        { id: '1', label: '已发送' },
      ],
      // 查询参数
      queryItems: {},
      // 表格数据
      tableData: [],
      // 选中列表数据
      selTableData: [],
      // 烟叶类型
      leafTypeList: [],
    };
  },
  computed: {
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.DAILY_YEAR;
    },
    bizCode() {
      return this.$store.getters.getAppConfig?.bizCode;
    },
  },
  created() {
    this.handleQueryBusinessOrgList();
    this.handleQueryIndustryOrgList();
    this.handleQueryLeafTypeList();
    this.onQuery();
  },
  methods: {
    // 查询烟叶类型
    handleQueryLeafTypeList() {
      this.$apis.bim.leafType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
      });
    },
    // 查询商业单位
    async handleQueryBusinessOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'TOBACCO',
        parentBizCode: this.bizCode,
        orgLevelEk: '2',
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.businessOrgList = res.rows || [];
    },
    // 更新商业单位
    onFetchBusinessOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.businessOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 查询工业单位
    async handleQueryIndustryOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'INDUSTRY',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.industryOrgList = res.rows || [];
    },
    // 更新工业单位
    onFetchIndustryOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.industryOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 查询
    onQuery() {
      const params = {
        busiYear: this.busiYear,
        ...this.queryItems,
      };
      $ec.showLoading();
      this.$apis.apm.baseDailyMaintain
        .query(params)
        .then((res) => {
          $ec.hideLoading();
          this.tableData = res?.rows || [];
        })
        .catch(() => {
          $ec.hideLoading();
        });
    },
    // 重置
    onReset() {
      this.queryItems = {};
      this.onQuery();
    },
    // 生成
    onGenerate() {
      const params = {
        busiYear: this.busiYear,
      };
      const content = () => (
        <div style="color:red;">是否确定生成？，确认请点击'确认'按钮，取消点击'取消'按钮</div>
      );

      this.$confirm({
        title: '是否执行该操作?',
        content,
        onOk: () => {
          $ec.showLoading();
          this.$apis.apm.baseDailyMaintain.generate(params).then((res) => {
            if (res.result) {
              this.$message.success('生成成功');
            } else {
              this.$message.error('生成失败');
            }
            $ec.hideLoading();
            this.onQuery();
          });
        },
      });
    },
    // 新增
    onAdd() {
      this.$refs.editModalRef.show();
    },
    // 删除
    onDelete() {
      if (!this.selTableData.length) {
        this.$message.error('请选择一条数据再删除');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认删除选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const params = {
            bmUnitApprQtyIds: this.selTableData.map((item) => item.bmUnitApprQtyId),
          };
          this.$apis.apm.baseDailyMaintain.delete(params).then(() => {
            this.$message.success('删除成功');
            $ec.hideLoading();
            this.onQuery();
          });
        },
      });
    },
    // 发送
    onSend() {
      if (!this.selTableData.length) {
        this.$message.error('请选择一条数据再发送');
        return;
      }
      if (this.selTableData.some((item) => item.transStates === '1')) {
        this.$message.error('未发送的数据才可进行操作');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认发送选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const params = {
            busiYear: this.busiYear,
            pcTBmUnitApprQtyList: this.selTableData,
          };
          this.$apis.apm.baseDailyMaintain.send(params).then(() => {
            this.$message.success('发送成功');
            $ec.hideLoading();
            this.onQuery();
          });
        },
      });
    },
    // 编辑
    onEdit(data) {
      console.log('data', data);
    },
    // table选中对应项
    onSelTableData({ records }) {
      this.selTableData = records;
    },
    // table选中所有项
    onSelAllTableData({ records }) {
      this.selTableData = records;
    },
  },
};
</script>
<style lang="less" scoped>
.table-bar-icon-btn {
  width: 32px;

  > i {
    margin-left: -4px;
  }
}
/deep/.ant-modal-body {
  padding: 6px 24px;
}
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
